  checkFileExt(fileext, acceptpdf) {
    if (acceptpdf == 0 || acceptpdf == 2) {
      if (fileext == 'png' || fileext == 'jpeg' || fileext == 'jpg') {
        return true;
      } else {
        return false;
      }
    } else {
      if (fileext == 'pdf') {
        return true;
      } else {
        return false;
      }
    }
  }
